import React from "react";
import { signOutUser } from "./../../../Firebase/auth";

const handleSignOut = () => {
  signOutUser();
};

const SignOut = () => {
  return (
    <div className="px-4">
      <button onClick={handleSignOut} className="btn btn-primary">
        Sign out
      </button>
    </div>
  );
};

export default SignOut;
