import { auth } from "./firebase";
import UIHandler from "./../Helpers/UIHandler";

setTimeout(function () {
  const messagesElement = document.querySelector(".messages");
  messagesElement.addEventListener("click", function (event) {
    if (event.target.parentElement.classList.contains("close-btn"))
      UIHandler.removeMessage();
  });
}, 100);

//Start of Email Related Auth
export function createUserWithEmailAndPassword(email, password) {
  auth()
    .createUserWithEmailAndPassword(email, password)
    .then(function (result) {
      console.log(result);
      verifyEmail();
    })
    .catch(function (error) {
      // Handle Errors here.
      // var errorCode = error.code;
      var errorMessage = error.message;
      UIHandler.showMessage(errorMessage);
      // ...
    });
}

// export function signupWithEmailAndPassword(email, password) {
//   return auth().createUserWithEmailAndPassword(email, password);
// }

export function signinWithEmailAndPassword(email, password) {
  auth()
    .signInWithEmailAndPassword(email, password)
    .then(function (result) {
      if (!result.user.emailVerified) verifyEmail();
      if (result.user.emailVerified && result.credential)
        UIHandler.showMessage("Signed In With Email And Password");
    })
    .catch(function (error) {
      // Handle Errors here.
      // var errorCode = error.code;
      var errorMessage = error.message;
      UIHandler.showMessage(errorMessage);
      // ...
    });
}

// Pending to be used
function verifyEmail() {
  var user = auth().currentUser;
  user
    .sendEmailVerification()
    .then(function () {
      // Email sent.
      UIHandler.showMessage(
        "Verification email sent. Please refresh before signing in."
      );
    })
    .catch(function (error) {
      // An error happened.
      UIHandler.showMessage("Error in sending verification email");
    });
}

//End of Email Related Auth

