//Start of Sign out function
export function signOutUser() {
  auth()
    .signOut()
    .then(function () {
      UIHandler.showMessage("Signed out");
    })
    .catch(function (error) {
      UIHandler.showMessage("Failed to sign out");
    });
}
//End of Sign out function

//Start of Reset Password
export function resetPassword(email) {
  var emailAddress = email;

  auth()
    .sendPasswordResetEmail(emailAddress)
    .then(function () {
      UIHandler.showMessage("Reset instructions have been sent to the Email");
    })
    .catch(function (error) {
      // An error happened.
      UIHandler.showMessage(error.errorMessage);
    });
}
//End of Reset Password
