import React, { Component } from "react";
import FormGroup from "../form-group/form-group.component";

//Firebase
import { createUserWithEmailAndPassword } from "./../../Firebase/auth";
//UIHandler
import UIHandler from "./../../Helpers/UIHandler";

class SignUp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      confirm_password: "",
    };
  }

  handleChange = (e) => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  handleSubmit = async (event) => {
    event.preventDefault();
    if (this.state.password === this.state.confirm_password) {
      createUserWithEmailAndPassword(this.state.email, this.state.password);
      this.setState({ email: "", password: "", confirm_password: "" });
    } else UIHandler.showMessage("Passwords don't match");
  };

  render() {
    return (
      <div className="px-3 sign-in">
        <h2>Sign Up</h2>
        <span>I don't have an acoount</span>
        <form autoComplete="false" className="my-2">
          <FormGroup name="email" onChange={this.handleChange} type="email">
            {" "}
            Email
          </FormGroup>

          <FormGroup
            name="password"
            onChange={this.handleChange}
            type="password"
          >
            {" "}
            Password{" "}
          </FormGroup>
          <FormGroup
            name="confirm_password"
            onChange={this.handleChange}
            type="password"
          >
            Confirm Password{" "}
          </FormGroup>
        </form>
        <button
          onClick={this.handleSubmit}
          className="btn btn-primary"
          type="submit"
        >
          Sign Up
        </button>
      </div>
    );
  }
}

export default SignUp;
