import React from "react";
import Homepage from "./Pages/Homepage/homepage";
import SignInUp from "./Pages/Sign-in/sign-in-Page.page";
import SignOut from "./Pages/Sign-in/Sign-out/signOut";
import ForgetPassword from "./Pages/Sign-in/Reset Password/reset-password.page";
import Header from "./Components/Header/header.component";
import "./App.scss";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import Products from "./Resources/data";
import ProductsPage from "./Components/ProductsPages/ProductsPage.component";

//Firebase
import { auth } from "./Firebase/firebase";
import {
  // addUser,
  // getDataAllUsers,
  updateUser,
  // getDataOfUser,
  // deleteUserData,
} from "./Firebase/db";
import PrivateRoute from "./Firebase/PrivateRoute.component";
import PublicRoute from "./Firebase/PublicRoute.component";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      authenticated: false,
      loading: true,
    };
  }

  componentDidMount() {
    auth().onAuthStateChanged((user) => {
      if (user && user.emailVerified) {
        this.setState({
          authenticated: true,
          loading: false,
        });
        updateUser(user.email, {
          carts: {
            hats: {
              "Nike Hat": 2,
              "Puma Hat": 5,
            },
          },
        });
      } else {
        this.setState({
          authenticated: false,
          loading: false,
        });
      }
    });
  }

  render() {
    const { hats, men, women, shoes } = Products.products;
    return (
      <BrowserRouter>
        <div className="App">
          <Header />

          <div className="messages py-2 px-5">
            {/* <div className="message py-2 px-3">
              You are signed in.
              <div className="close-btn">
                <ion-icon name="close-circle-outline"></ion-icon>
              </div>
            </div> */}
          </div>

          <Switch>
            <Route path="/" exact>
              <Homepage />
            </Route>
            <Route path="/hats" exact>
              <ProductsPage category="Hats" products={hats} />;
            </Route>
            <Route path="/shoes" exact>
              <ProductsPage category="shoes" products={shoes} />;
            </Route>
            <Route path="/men" exact>
              <ProductsPage category="Men" products={men} />;
            </Route>
            <Route path="/women" exact>
              <ProductsPage category="Women" products={women} />;
            </Route>

            {/* Different Links of Menu */}
            <Route path="/shop" exact>
              <ProductsPage category="Hats" products={hats} max={4} />;
              <ProductsPage category="shoes" products={shoes} max={4} />;
              <ProductsPage category="Women" products={women} max={4} />;
              <ProductsPage category="Men" products={men} max={4} />;
            </Route>

            <PrivateRoute
              path="/sign-out"
              exact
              authenticated={this.state.authenticated}
              component={SignOut}
            ></PrivateRoute>

            <PublicRoute
              path="/sign-in"
              exact
              authenticated={this.state.authenticated}
              component={SignInUp}
            ></PublicRoute>

            <PublicRoute
              path="/forget-password"
              exact
              authenticated={this.state.authenticated}
              component={ForgetPassword}
            ></PublicRoute>
          </Switch>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
