import { db } from "./firebase";
import UIHandler from "../Helpers/UIHandler";

// Start of adding user
export function addUser(email, data) {
  db.collection("users")
    .doc(email)
    .set(data)
    .then(function (docRef) {
      console.log("Successfully added");
    })
    .catch(function (error) {
      console.error("Error adding document: ", error);
    });
}
// End of adding user

// Start of update user

// data = {
//   favorites: {
//     food: "Ice Cream",
//   },
// };
export function updateUser(email, data) {
  db.collection("users")
    .doc(email)
    .update(data)
    .then(function () {
      UIHandler.showMessage("Data Updated");
    })
    .catch(function (error) {
      UIHandler.showMessage(error);
    });
}
// End of update user

// Start of Showing data of All users
export function getDataAllUsers(email) {
  db.collection("users")
    .get()
    .then((querySnapshot) => {
      if (email) console.log(querySnapshot);
      // email is not passed
      else
        querySnapshot.forEach((doc) => {
          console.log(`${doc.id} => ${doc.data().name}`);
        });
    });
}
// End of Showing data of All users

// Start of Showing data of a user
export function getDataOfUser(email) {
  console.log(email);
  var docRef = db.collection("users").doc(email);
  docRef
    .get()
    .then(function (doc) {
      if (doc.exists) {
        console.log("Document data:", doc.data());
      } else {
        // doc.data() will be undefined in this case
        console.log("No such document!");
      }
    })
    .catch(function (error) {
      console.log("Error getting document:", error);
    });
}
// End of Showing data of a user

// Start of clear carts of a user
export function clearUserData(email) {
  db.collection("users")
    .doc(email)
    .set({
      carts: {},
    })
    .then(function () {
      console.log("Document successfully deleted!");
    })
    .catch(function (error) {
      console.error("Error removing document: ", error);
    });
}
// End of clear carts of a user
