import React from "react";
import ProductPage from "../Product Page/Product.component";
import "./ProductsPage.styles.scss";
import { useHistory } from "react-router-dom";

export default function ProductsPage({ category, products, max }) {
  let history = useHistory();
  return (
    <div className="mx-5 my-3">
      <h2
        className="products-category"
        onClick={() => history.push("/" + category)}
      >
        #{category}
        {max !== undefined ? (
          <ion-icon name="arrow-forward-circle-outline"></ion-icon>
        ) : null}
      </h2>
      <div className="products-container">
        {products.map(({ name, img, id, price }, index) => {
          if (max === undefined || max > index)
            return (
              <ProductPage
                key={id}
                name={name}
                img={img}
                id={id}
                price={price}
              />
            );
          return 0;
        })}
      </div>
    </div>
  );
}
